#partners {
  position: relative;

  & .container-full {
    padding-bottom: 0;
    position: relative;
    background-image: url(../images/partners/partners-bg.png);
    background-color: #4690ff;
    background-repeat: no-repeat;
    background-position: top center;

    @media (max-width: $desktop-small) {
      background-image: url(../images/partners/partners-bg-mobile.png);
      background-size: 1550px;
    }

    @media (max-width: $mobile) {
      padding-top: 50px;
      background-size: unset;
    }
  }

  & .wrapper__container {
    overflow: hidden;
    padding-bottom: 200px;

    min-height: 2280px;

    @media (max-width: $mobile) {
      min-height: 1880px;
      background-image: url(../images/partners/bg-bottom-mobile.png);
      background-repeat: no-repeat;
      background-position: center bottom;
    }
  }
}

.partners__header {
  max-width: 600px;

  @media (max-width: $desktop-small) {
    margin: 0 auto;
  }

  @media (max-width: $mobile) {
    margin: unset;
    max-width: 350px;
  }
}

.partners__banner-row {
  display: flex;
  align-items: flex-end;

  @media (max-width: $desktop-small) {
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: $mobile) {
    padding-left: 24px;
    padding-right: 24px;
  }
}

.partners__banner-left {
  max-width: 470px;
  padding-bottom: 60px;

  @media (max-width: $desktop-small) {
    padding-bottom: 0;
  }
}

.partners__banner-right {
  @media (max-width: $mobile) {
    margin-left: -24px;
    margin-right: -24px;
  }
}

.partners__banner {
  position: relative;
  margin-top: 60px;
  padding-top: 60px;
  padding-left: 60px;

  color: $thirdColor;

  background-color: $primaryColor;

  border-radius: 36px;

  @media (max-width: $desktop-small) {
    margin-top: 200px;
    padding-left: 0;
  }

  @media (max-width: $mobile) {
    margin-top: 180px;
    padding-top: 24px;
  }
}

.partners__banner-header {
  @media (max-width: $desktop-small) {
    text-align: center;
  }

  @media (max-width: $desktop-small) {
    font-size: 22px;
    max-width: 245px;
    margin: 0 auto;

    & h2 {
      font-size: 22px;
    }
  }
}

.partners__banner-content {
  display: flex;
  flex-direction: column;

  margin-top: 40px;
}

.partners__banner-content > * + * {
  margin-top: 40px;
}

.partners-steps {
  display: flex;

  & p {
    font-family: normalFont;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.3;
    padding-left: 40px;

    @media (max-width: $mobile) {
      padding-left: 21px;
      font-size: 18px;
    }
  }

  & span {
    font-weight: 600;
    font-size: 40px;

    color: #519ef8;

    @media (max-width: $mobile) {
      font-size: 28px;
    }
  }
}

.partners__cloud {
  position: absolute;
  bottom: -100px;
  right: -180px;

  @media (max-width: $desktop-small) {
    display: none;
  }
}

.partners__list {
  margin-top: 80px;

  & h2 {
    max-width: 600px;

    @media (max-width: $desktop-small) {
      margin: 0 auto;
      text-align: center;
    }

    @media (max-width: $mobile) {
      // text-align: unset;
    }
  }

  @media (max-width: $mobile) {
    margin-top: 50px;
  }
}

.partners__pattern {
  min-width: 1168px;
  margin-top: 60px;

  @media (max-width: 1250px) {
    max-width: 1168px;
    min-width: unset;
  }

  @media (max-width: $desktop-small) {
    max-width: 590px;
    margin-left: auto;
    margin-right: auto;
  }

  @media (max-width: $laptop) {
    max-width: 555px;
  }

  @media (max-width: 622px) {
    max-width: 370px;
    // margin-left: unset;
  }

  @media (max-width: $mobile) {
    max-width: 338px;
    margin-top: 25px;
  }

  @media (max-width: 403px) {
    max-width: 300px;
    margin-top: 25px;
  }

  @media (max-width: 364px) {
    max-width: 150px;
    margin-top: 25px;
  }
}

.pattern-item {
  max-width: 262px;
  border-radius: 50px;

  @media (max-width: $laptop) {
    max-width: 155px;
  }

  @media (max-width: $mobile) {
    max-width: 155px;
  }

  @media (max-width: 403px) {
    max-width: 135px;
  }

  @media (max-width: 364px) {
    max-width: 150px;
  }
}

.pattern__row {
  display: flex;
  align-items: center;
}

.pattern__row > * + * {
  margin-left: 30px;
}

.partners__pattern > * + * {
  margin-top: 30px;
}

.pattern__top {
  display: flex;
}

.pattern__top > * + * {
  margin-left: 30px;
}

.pattern__rows {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $desktop-small) {
    // margin-right: auto;
    // margin-left: auto;
    // width: 70%;
  }
}

.pattern__rows > * {
  margin: 15px;

  @media (max-width: $mobile) {
    margin: 7px;
  }
}

.partners__quote {
  margin-top: 70px;
  max-width: 574px;

  margin-left: auto;
  margin-right: auto;
  & span {
    display: block;
    color: rgba(255, 255, 255, 0.6);
    font-size: 24px;

    @media (max-width: $mobile) {
      font-size: 18px;
    }
  }

  @media (max-width: $desktop-small) {
    text-align: center;
  }

  @media (max-width: $mobile) {
    text-align: unset;
    font-size: 18px;
    line-height: 1.3;
  }

  @media (max-width: 364px) {
    text-align: center;
  }
}

.partners__quote > * + * {
  margin-top: 30px;

  @media (max-width: $mobile) {
    margin-top: 15px;
  }
}

#partners .bg-bottom {
  display: none;

  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  @media (max-width: $mobile) {
    display: block;
  }
}
