#getting-started {
  overflow: hidden;
  background-color: #6037cc;

  & .section {
    margin: 0 -180px;
  }

  & .wrapper__container {
    @media (max-width: 1250px) {
      max-width: 1100px;
    }

    @media (max-width: $mobile) {
      max-width: 340px;
    }
  }
}

#getting-started .first__section {
  -webkit-transform: rotate(-13deg);
  -moz-transform: rotate(-13deg);
  -o-transform: rotate(-13deg);
  -ms-transform: rotate(-13deg);
  transform: rotate(-13deg);
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.4) inset;

  & .container-full {
    padding-bottom: 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: #6037cc;
    background-image: url(../images/getting-started/first-bg.png);
    background-repeat: no-repeat;
    background-position: top center;

    margin-top: 0;

    -webkit-transform: rotate(13deg);
    -moz-transform: rotate(13deg);
    -o-transform: rotate(13deg);
    -ms-transform: rotate(13deg);
    transform: rotate(13deg);

    @media (max-width: $desktop-small) {
      padding-top: 40px;
      padding-bottom: 120px;
      background-image: url(../images/getting-started/first-bg-mobile.png);
      background-size: 1170px;
    }

    @media (max-width: $mobile) {
      background-size: unset;
    }
  }

  & .wrapper__container {
    min-height: 625px;

    @media (max-width: $desktop-small) {
      min-height: 750px;
    }

    @media (max-width: $mobile) {
      min-height: 510px;
    }
  }
}

#getting-started .second__section {
  margin-top: -130px;

  -webkit-transform: rotate(-13deg);
  -moz-transform: rotate(-13deg);
  -o-transform: rotate(-13deg);
  -ms-transform: rotate(-13deg);
  transform: rotate(-13deg);

  & .container-full {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    padding-bottom: 0;
    padding-top: 0;

    margin-top: 0;
    background: #794af1;
    box-shadow: -4px -100px 135px 55px rgb(0 0 0 / 12%) inset;

    @media (max-width: 1250px) {
      padding-top: 40px;
    }

    @media (max-width: $desktop-small) {
      padding-top: 100px;
    }

    @media (max-width: $mobile) {
      padding-top: 55px;
    }
  }

  & .section-wrapper {
    align-items: flex-end;

    @media (max-width: $desktop-small) {
      flex-direction: column;
    }

    @media (max-width: $desktop-small) {
      & > * + * {
        margin-top: 40px;
      }
    }
  }

  & .wrapper__container {
    padding-left: 20px;
    min-height: 675px;
    display: flex;
    align-items: center;
    // justify-content: flex-end;

    & .section-img {
      margin-bottom: -21px;

      @media (max-width: $desktop-small) {
        margin-bottom: -62px;
      }
    }

    -webkit-transform: rotate(13deg);
    -moz-transform: rotate(13deg);
    -o-transform: rotate(13deg);
    -ms-transform: rotate(13deg);
    transform: rotate(13deg);

    @media (max-width: 1250px) {
      min-height: 600px;
    }

    @media (max-width: $desktop-small) {
      justify-content: center;
    }

    @media (max-width: $mobile) {
      min-height: 535px;
      padding-left: 10px;
    }
  }
}

#getting-started .third__section {
  // margin-top: -870px;

  background-color: #7f4dfe;

  & .container-full {
    padding-top: 0;
    padding-bottom: 130px;

    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: #7f4dfe;

    margin-top: 0;

    -webkit-transform: rotate(-13deg);
    -moz-transform: rotate(-13deg);
    -o-transform: rotate(-13deg);
    -ms-transform: rotate(-13deg);
    transform: rotate(-13deg);

    @media (max-width: $desktop-small) {
      padding-bottom: 100px;
    }
  }

  & .wrapper__container {
    min-height: 675px;
    padding-left: 35px;

    display: flex;
    align-items: flex-end;

    -webkit-transform: rotate(13deg);
    -moz-transform: rotate(13deg);
    -o-transform: rotate(13deg);
    -ms-transform: rotate(13deg);
    transform: rotate(13deg);

    @media (max-width: 1250px) {
      min-height: 600px;
    }

    @media (max-width: $desktop-small) {
      justify-content: center;
      padding-top: 130px;
    }

    @media (max-width: $mobile) {
      justify-content: center;
      padding-top: 0;
      padding-left: 25px;
      min-height: 535px;
    }
  }

  & .section-wrapper {
    @media (max-width: $desktop-small) {
      flex-direction: column;

      & > * + * {
        margin-top: 30px;
      }
    }
  }

  & .section-content span {
    color: #ffdf38;
  }
}

#getting-started .section-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#getting-started .section-content {
  max-width: 498px;

  & h2 {
    color: #fa90d1;

    @media (max-width: $desktop-small) {
      font-size: 42px;
    }

    @media (max-width: $mobile) {
      font-size: 28px;
    }
  }

  @media (max-width: $desktop-small) {
    font-size: 28px;
    margin: 0 auto;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

#getting-started .section-content > * + * {
  margin-top: 30px;

  @media (max-width: $mobile) {
    margin-top: 14px;
  }
}
