#header {
  padding-top: 25px;

  min-height: 1320px;

  background-image: url(../images/header/header-bg.png);
  background-repeat: no-repeat;
  background-position: bottom center;

  @media (max-width: $mobile) {
    background-image: url(../images/header/header-bg-mobile.png);
    min-height: 600px;
  }
}

.header__nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  & .button {
    background-color: $thirdColor;

    @media (max-width: $mobile) {
      display: none;
    }
  }

  @media (max-width: $mobile) {
    justify-content: center;
  }
}

.header__logo {
  display: block;

  @media (max-width: $mobile) {
    display: none;
  }
}

.header__logo-mobile {
  display: none;

  & img {
    width: 150px;
  }

  @media (max-width: $mobile) {
    display: block;
  }
}

.header__content {
  padding-top: 75px;
  color: $thirdColor;

  & .title-h1 {
    text-align: center;
    max-width: 740px;

    margin-left: auto;
    margin-right: auto;

    @media (max-width: $mobile) {
      font-size: 32px;
    }
  }

  @media (max-width: $mobile) {
    padding-top: 20px;
    padding-bottom: 300px;
  }
}

.header__content > * + * {
  margin-top: 30px;

  @media (max-width: $mobile) {
    margin-top: 14px;
  }
}

.header__button {
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;

  & a {
    display: inline-block;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.header__button > * + * {
  margin-top: 40px;

  @media (max-width: $mobile) {
    margin-top: 14px;
  }
}

.header__marquee {
  padding-top: 25px;
  padding-bottom: 25px;

  background-color: #bcff50;

  color: $thirdColor;

  overflow: hidden;

  @media (max-width: $mobile) {
    font-size: 17px;
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

@keyframes moveSlideshow {
  100% {
    transform: translateX(-3000px);
  }
}

.marquee__content {
  display: flex;
  animation: moveSlideshow 60s linear infinite;
}

.marquee__content > * + * {
  margin-left: 16px;
}

.marquee-item {
  display: flex;
  align-items: center;

  flex-shrink: 0;

  & img{

    @media (max-width: $mobile) {
      width: 6px;
    }
  }
}

.marquee-item > * + * {
  margin-left: 16px;
}
