#how {
  position: relative;
  background-color: $secondaryColor;
  color: $primaryColor;

  & .container-full {
    background-color: $secondaryColor;

    @media (max-width: $mobile) {
      padding-top: 40px;
    }
  }
}

.how__header {
  @media (max-width: $desktop-small) {
    text-align: center;
  }
}

.how__content {
  margin-top: 60px;

  @media (max-width: $mobile) {
    margin-top: 30px;
  }
}

.how__content > * + * {
  margin-top: 40px;

  @media (max-width: $desktop-small) {
    margin-top: 25px;
  }
}

.how__row {
  display: flex;
  justify-content: space-between;

  @media (max-width: $desktop-small) {
    flex-direction: column;
    & > * + * {
      margin-top: 25px;
    }
  }
}

.how__item {
  display: flex;
  position: relative;

  & span {
    position: absolute;
    top: 0;
    left: 0;

    font-weight: 600;
    font-size: 56px;
    line-height: 1.05;

    color: rgba(255, 255, 255, 0.5);

    @media (max-width: $desktop-small) {
      position: relative;
    }

    @media (max-width: $mobile) {
      font-size: 28px;
    }
  }

  & p {
    padding-left: 70px;
    max-width: 560px;

    @media (max-width: $desktop-small) {
      padding-left: 58px;
    }

    @media (max-width: $mobile) {
      padding-left: 23px;
    }
  }

  @media (max-width: $desktop-small) {
    justify-content: center;
  }

  @media (max-width: $mobile) {
    font-size: 18px;
    justify-content: flex-start;
  }
}

.how__banner {
  position: relative;
  padding: 60px;
  margin-top: 60px;

  background-color: $bg-color;
  background-image: url(../images/how/bg-how.png);
  background-position: right bottom;
  background-repeat: no-repeat;

  border-radius: 36px;
  color: $thirdColor;
  font-size: 24px;

  & .title-h3 {
    color: $secondaryColor;
    max-width: 406px;

    @media (max-width: $desktop-small) {
      max-width: unset;
    }

    @media (max-width: $mobile) {
      font-size: 22px;
    }
  }

  @media (max-width: $desktop-small) {
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    max-width: 600px;
    min-height: 1050px;

    background-image: url(../images/how/bg-how-mobile.png);
    background-position: center bottom;
    background-size: 1100px;
  }

  @media (max-width: $mobile) {
    padding: 24px;
    min-height: 719px;
    background-size: unset;
  }
}

.how__banner-content {
  max-width: 600px;

  & .button {
    margin-top: 40px;
    display: inline-block;
  }

  @media (max-width: $desktop-small) {
    text-align: center;
    padding-bottom: 300px;
  }
}

.how__banner-content > * + * {
  margin-top: 20px;
}

.how__banner-info {
  & p:not(:first-child) {
    max-width: 525px;
  }

  @media (max-width: $mobile) {
    font-size: 18px;

    & .title-thin {
      font-size: 18px;
    }
  }
}

.how__banner-info > * + * {
  margin-top: 20px;
}

.how__image {
  position: absolute;
  bottom: 0;
  right: 0;

  @media (max-width: $desktop-small) {
    display: none;
  }
}
