#multiply {
  position: relative;
  & .container-full {
    padding-bottom: 0;
    min-height: 975px;
    background-color: #4924a9;

    background-image: url(../images/multiply/multiply-bg.png);
    background-repeat: no-repeat;
    background-position: center;

    @media (max-width: $desktop-small) {
      min-height: 820px;
      background-size: 1400px;
    }

    @media (max-width: $mobile) {
      padding-top: 40px;
      min-height: 545px;
      padding-bottom: 25px;

      background-image: url(../images/multiply/multiply-bg-mobile.png);
    }
  }

  & .wrapper__container {
    background-image: url(../images/multiply/multiply-phones.png);
    background-repeat: no-repeat;
    background-position: center bottom;
    min-height: 1038px;

    @media (max-width: $mobile) {
      background-image: url(../images/multiply/multiply-phones-mobile.png);
      min-height: 545px;
    }
  }
}

.multiply__header {
  text-align: center;
  max-width: 650px;
  margin-left: auto;
  margin-right: auto;

  & .button {
    display: inline-block;
    background-color: $primaryColor;
    color: $thirdColor;
  }

  @media (max-width: $mobile) {
    max-width: 302px;
  }
}

.multiply__header > * + * {
  margin-top: 40px;
}

.multiply__img {
  display: block;

  // @media (max-width: $desktop-small) {
  //   display: none;
  // }
}

.multiply__img-mobile {
  display: none;
  // @media (max-width: $desktop-small) {
  //   display: block;
  // }
}
