#faq {
  color: $thirdColor;

  & h2 {
    margin: 0 auto;
    // font-size: 56px;
  }

  & .container-full {
    position: relative;
    width: 100%;
    padding-top: 120px;
    padding-bottom: 120px;

    border-top-left-radius: 36px;
    border-top-right-radius: 36px;
    background-color: $bg-color;

    margin-top: -50px;

    @media (max-width: $laptop) {
      padding-top: 70px;
    }

    @media (max-width: $mobile) {
      padding-top: 40px;
      padding-bottom: 80px;
    }
  }
}

.faq__buttons {
  margin-top: 40px;

  @media (max-width: $mobile) {
    margin-top: 24px;
  }
}

.faq__buttons > * + * {
  margin-top: 24px;

  @media (max-width: $mobile) {
    margin-top: 16px;
  }
}

.faq__img {
  display: block;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: $mobile) {
    display: none;
  }
}

.faq__img-mob {
  display: none;
  margin-top: 24px;
  margin-left: auto;
  margin-right: auto;

  & img {
    margin-left: auto;
    margin-right: auto;
  }

  @media (max-width: $mobile) {
    display: block;
  }
}

.faq__questions {

  & h2{
    @media (max-width: $desktop-small) {
      text-align: center;
    }
  }
  @media (max-width: $laptop) {
    margin-top: 70px;
  }

  @media (max-width: $mobile) {
    margin-top: 0;
  }
}

.faq__questions-wrapper {
  margin-top: 60px;

  @media (max-width: $mobile) {
    margin-top: 24px;
  }
}

.faq__questions-wrapper > * + * {
  margin-top: 30px;
}

.faq-question {
  padding: 60px;
  padding-top: 70px;

  background-color: $primaryColor;
  border-radius: 60px;

  display: flex;
  flex-direction: column;

  @media (max-width: $mobile-big) {
    padding: 30px;
    padding-top: 60px;
  }

  @media (max-width: $mobile) {
    padding: 24px;
    padding-top: 40px;
  }
}

.faq-question > * + * {
  margin-top: 20px;
}

.faq-question-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  cursor: pointer;

  & h5 {
    max-width: 750px;

    @media (max-width: $mobile) {
      font-size: 18px;
      max-width: 320px;
    }
  }

  & .question-arrow {
    transform: rotate(180deg);
    transition: all 0.5s ease-in-out;

    @media (max-width: $mobile) {
      width: 36px;
      flex-shrink: 0;
    }
  }

  & .question-arrow.active {
    transform: rotate(0);
    transition: all 0.5s ease-in-out;
  }
}

.faq-question-info {
  max-width: 750px;
  max-height: 0px;

  font-weight: 400;
  font-size: 24px;
  line-height: 1.3;

  color: rgba(0, 0, 0, 0.7);
  transition: max-height 0.5s ease-in;
  overflow: hidden;

  @media (max-width: $mobile) {
    font-size: 18px;
  }
}

.faq-question-info.active {
  max-height: 500px;
  -webkit-transition: max-height 0.5s ease-in;
  transition: max-height 0.5s ease-in;
}

.question-arrow {
  @media (max-width: $mobile-big) {
    width: 120px;
  }
}

