#benefits {
  color: $thirdColor;
  position: relative;

  & .container-full {
    background-image: url(../images/benefits/benefits-bg1.png);
    background-repeat: no-repeat;
    background-position: top center;

    @media (max-width: $desktop-small) {
      background-size: 1400px;
    }

    @media (max-width: $mobile) {
      padding-top: 90px;
    }
  }
}

.benefits__wrapper > * + * {
  margin-top: 60px;

  @media (max-width: $mobile) {
    margin-top: 24px;
  }
}

.benefits__header {
  max-width: 450px;

  @media (max-width: $desktop-small) {
    margin: 0 auto;
    text-align: center;
  }

  @media (max-width: $mobile) {
    max-width: 300px;
  }
}

.benefits__row {
  display: flex;

  @media (max-width: $desktop-small) {
    flex-direction: column;
  }
}

.benefits__row > * + * {
  margin-left: 30px;

  @media (max-width: $desktop-small) {
    margin-top: 30px;
    margin-left: 0;
  }
}

.benefits-item-left,
.benefits-item-right {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding-top: 60px;
  padding-right: 60px;
  padding-left: 60px;

  border-radius: 50px;
  font-size: 28px;
  color: $primaryColor;

  min-height: 668px;

  @media (max-width: $mobile) {
    padding-top: 24px;
    padding-right: 24px;
    padding-left: 24px;

    min-height: 374px;
  }
}

.benefits-item-left {
  background-image: url(../images/benefits/benefits-bg-1.png);

  & span {
    color: #bcc2ce;
  }

  & .benefits-item__img {
    @media (max-width: $mobile) {
      max-width: 238px;
    }
  }

  @media (max-width: $mobile) {
    background-image: url(../images/benefits/benefits-bg-1-mobile.png);
    background-repeat: no-repeat;
    background-position: top center;
  }
}

.benefits-item-right {
  background-image: url(../images/benefits/benefits-bg-2.png);

  & span {
    color: #fa90d1;
  }

  & .benefits-item__img {
    @media (max-width: $mobile) {
      max-width: 215px;
    }
  }

  @media (max-width: $mobile) {
    background-image: url(../images/benefits/benefits-bg-2-mobile.png);

    background-repeat: no-repeat;
    background-position: top center;
  }
}

.benefits-item__img {
  margin-left: auto;
  margin-right: auto;
}

.benefits-item__text {
  @media (max-width: $mobile) {
    font-size: 18px;
  }
}
