#footer {
  & .container-full {
    padding-bottom: 0;

    @media (max-width: $mobile) {
      padding-top: 40px;
    }
  }

  & .wrapper__container{
    padding-bottom: 60px;
  }
}

.footer__contacts {
  display: flex;

  color: $thirdColor;
  & a {
    color: $thirdColor;
  }

  @media (max-width: $mobile-big) {
    flex-direction: column-reverse;
  }
}

.footer__contacts > * + * {
  margin-left: 60px;

  @media (max-width: $laptop) {
    margin-left: 30px;
  }

  @media (max-width: $mobile-big) {
    margin-left: unset;
  }
}

.footer__top {
  padding-bottom: 20px;

  display: flex;
  justify-content: space-between;

  @media (max-width: $mobile-big) {
    flex-direction: column;
  }
}

.footer__top > * + * {
  margin-left: 20px;

  @media (max-width: $mobile-big) {
    margin-left: unset;
    margin-top: 18px;
  }

  @media (max-width: $mobile) {
    margin-top: 5px;
  }
}

.footer__phone {
  max-width: 360px;
  & p {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.4;

    color: #898ba2;
  }

  @media (max-width: $mobile) {
    font-size: 16px;
  }
}

.footer__phone > * + * {
  margin-top: 14px;
}

.footer__socials {
  & .vk {
    color: $secondaryColor;
  }

  & .tg {
    color: #60b3ed;
  }
}

.footer__socials > * + * {
  margin-left: 46px;

  @media (max-width: $laptop) {
    margin-left: 25px;
  }
}

.footer__docs {
  display: flex;

  & a {
    font-size: 16px;

    color: #898ba1;
  }

  @media (max-width: $mobile-big) {
    padding-bottom: 18px;
    flex-direction: column;
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  }
}

.footer__docs > * + * {
  margin-left: 58px;

  @media (max-width: $mobile-big) {
    margin-left: unset;
    margin-top: 18px;
  }
}

.footer__bottom {
  padding-top: 20px;
  padding-bottom: 20px;

  font-weight: 600;
  font-size: 16px;

  color: #898ba1;

  border-top: 1px solid rgba(0, 0, 0, 0.3);

  @media (max-width: $mobile-big) {
    border-top: none;
  }

  @media (max-width: $mobile) {
    border-top: none;
    padding-top: 0;
  }
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: $mobile-big) {
    flex-direction: column-reverse;
    align-items: flex-start;
  }
}

.footer__copyright {
  @media (max-width: $mobile-big) {
    padding-top: 18px;
  }
}

.footer__button {
  min-width: 285px;
  display: inline-block;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  padding-bottom: 14px;

  & a{
    font-weight: 500;
  font-size: 20px;
  }
  
}
